import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionBankCode , { ReduxEnumBankCode }  from '../../../_redux/action/reduxAction.bankCode';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';

import { DateUtil }		                            from '../../../_util/util.date';








/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';











import './s.bankCode.list.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxBankCode' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumBankCode.LIST_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentBankCode extends ComponentAbstract {


            constructor( props ) {
                super( props )

                this.state = {

                    data : { }


                    , form : { }


                    , result : []

                }

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestList();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */

            onClickDetail( bankCode ) {

                console.log( 'ComponentBankCode : onClickDetail : *** : bankCode => ' , bankCode );


                let record = {};
                for ( let i = 0; i < this.state.result.length; i ++ ) {
                    if ( this.state.result[ i ].bankCode == bankCode ) {
                        record = this.state.result[ i ];
                    }
                }                

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.COMPANY_ONBOARD_SUBMIT_DETAIL )
                    , record
                );

            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestList() {
                console.log( 'ComponentBankCode : requestList : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {

                                this.dispatch(
                                    ReduxActionBankCode.rList(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )

                            }
                        );

                    }
                )

            }
            requestListDone( result ) {
                console.log( 'ComponentBankCode : requestListDone : *** : result => ' , result );

                this.setState( {
                    isLoading   : false
                    , result    : result
                } );
                this.dispatch( ReduxActionBankCode.rRequestEnd( ) );
            }













            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper
                                    style={ { width : '90%', margin : '0px auto' } }>


                                <div className={ ' bankcode-padding-10 ' } >
                                    <h3>Bank Code</h3>
                                </div>


                                <div>



                                </div>


                                <Divider />


                                <Table
                                            fixedHeader={ false }
                                            selectable={ false }
                                            style={ { width: "100%" } }
                                            tableLayout="auto">

                                    <TableHeader
                                                    displaySelectAll={ false }
                                                    adjustForCheckbox={ false }>

                                        <TableRow>

                                            <TableHeaderColumn> Code </TableHeaderColumn>
                                            <TableHeaderColumn> Name </TableHeaderColumn>
                                            <TableHeaderColumn> IBG Routing Number </TableHeaderColumn>
                                            <TableHeaderColumn> RPP Code </TableHeaderColumn>

                                        </TableRow>

                                    </TableHeader>

                                    <TableBody

                                                displayRowCheckbox={ false }>

                                        {
                                            this.state.result.map(
                                                ( item , index ) => {
                                                    return (
                                                        <TableRow
                                                                    key={ '_bankCode_' + index } >

                                                            <TableRowColumn> { item.bankCode } </TableRowColumn>
                                                            <TableRowColumn> { item.bankName } </TableRowColumn>
                                                            <TableRowColumn> { item.routingNumber } </TableRowColumn>
                                                            <TableRowColumn> { item.rppCode } </TableRowColumn>

                                                        </ TableRow>
                                                    )
                                                }
                                            )
                                        }

                                    </TableBody>

                                </Table>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                        </div>
                )
            }
        }

    )



